/* UI elements "Buttons" */
.button {
    position: relative;
    display: block;
    border: none;
    border-radius: 999px;
    padding: 1.25em 2em 1.25em 2em;
    background-color: #787d46;
    text-align: center;
    cursor: pointer;
    transition: all 0.5s ease 0s;
    color: #ffffff;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    text-decoration: none;
    overflow: hidden;
}

.button::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 100%;
    transform: translateY(-50%);
    width: 100%;
    height: 100%;
    background-image: url("../images/bg/bg-button.svg");
    background-position: center;
    background-repeat: repeat-x;
    background-size: auto 90%;
    transition: inherit;
}

.button span {
    position: relative;
}

.button:hover {
    background-color: #676d23;
}

.button:hover::before {
    right: 0;
}

@media only screen and (min-width: 768px) {
    .button {
        font-size: 16px;
        line-height: 20px;
    }
}

